import './App.css';
import productss from './products.json'
import React, {Component} from 'react';
import { Switch, Route, Link, BrowserRouter as Router, Routes } from "react-router-dom";
import AddProduct from './components/AddProduct';
import Shoppingcart from './components/Shoppingcart';
import Login from './components/Login';
import ProductList from './components/ProductList';
import Context from './components/Context';
import axios from 'axios';
import jwt_decode from 'jwt-decode';
import Numerical from './numerical';

export default class App extends Component{
  constructor(props){
    super(props);
    this.state={
      user:null,
      cart: {},
      products: [],
      pages: 0
    };
    this.routerRef = React.createRef();
  }
 /* componentDidMount() {
    let user = localStorage.getItem("user");
    user = user ? JSON.parse(user) : null;
    this.setState({ user });
  }*/
  /*async componentDidMount() {
    let user = localStorage.getItem("user");
    const products = await axios.get('http://localhost:3001/products');
    user = user ? JSON.parse(user) : null;
    this.setState({ user,  products: products.data });
  }*/
  addToCart = cartItem => {
    let cart = this.state.cart;
    if (cart[cartItem.id]) {
      cart[cartItem.id].amount += cartItem.amount;
    } else {
      cart[cartItem.id] = cartItem;
    }
    if (cart[cartItem.id].amount > cart[cartItem.id].product.stock) {
      cart[cartItem.id].amount = cart[cartItem.id].product.stock;
    }
    localStorage.setItem("cart", JSON.stringify(cart));
    this.setState({ cart });
  };
  removeFromCart = cartItemId => {
    let cart = this.state.cart;
    delete cart[cartItemId];
    localStorage.setItem("cart", JSON.stringify(cart));
    this.setState({ cart });
  };  
  clearCart = () => {
    let cart = {};
    localStorage.removeItem("cart");
    this.setState({ cart });
  };
  async componentDidMount() {
    let user = localStorage.getItem("user");
    let cart = localStorage.getItem("cart");  
    //const products = await axios.get('http://localhost:3001/products');
    const products = productss.products;
    user = user ? JSON.parse(user) : null;
    cart = cart? JSON.parse(cart) : {};  
    this.setState({ user,  products: products/* products.data */, cart });
  }
  login = async (email, password) => {
    const res = await axios.post(
      'http://localhost:3001/login',
      { email, password },
    ).catch((res) => {
      return { status: 401, message: 'Unauthorized' }
    })
    if(res.status === 200) {
      const { email } = jwt_decode(res.data.accessToken)
      const user = {
        email,
        token: res.data.accessToken,
        accessLevel: email === 'admin@example.com' ? 0 : 1
      }  
      this.setState({ user });
      localStorage.setItem("user", JSON.stringify(user));
      return true;
    } else {
      return false;
    }
  }
  checkout = () => {
   if (!this.state.user) {
      this.routerRef.current.history.push("/login");
      return;
    }  
    const cart = this.state.cart;  
    const products = this.state.products.map(p => {
      if (cart[p.name]) {
        p.stock = p.stock - cart[p.name].amount;
  
        axios.put(
          `http://localhost:3001/products/${p.id}`,
          { ...p },
        )
      }
      return p;
    });  
    this.setState({ products });
    this.clearCart();
  };
  addProduct = (product, callback) => {
    let products = this.state.products.slice();
    products.push(product);
    this.setState({ products }, () => callback && callback());
  };
  logout = e => {
    e.preventDefault();
    this.setState({ user: null });
    localStorage.removeItem("user");
  };
  pageSwitch = () =>{
    if (this.state.pages===0) {
      this.setState({pages:1})
    }
    else
    this.setState({pages:0})
  }
  render(){
    if(this.state.pages===0)
    return (
      <Context.Provider
        value={{
          ...this.state,
          removeFromCart: this.removeFromCart,
          addToCart: this.addToCart,
          login: this.login,
          addProduct: this.addProduct,
          clearCart: this.clearCart,
          checkout: this.checkout
        }}>
        <Router ref={this.routerRef}>
        <div className="App">
          <nav
            className="navbar container"
            role="navigation"
            aria-label="main navigation">
            <div className="navbar-brand">
              <b className="navbar-item is-size-4 ">E-commerce</b>
              <label
                role="button"
                class="navbar-burger burger"
                aria-label="menu"
                aria-expanded="false"
                data-target="navbarBasicExample"
                onClick={e => {
                  e.preventDefault();
                  this.setState({ showMenu: !this.state.showMenu });
                }}>
                <span aria-hidden="true"></span>
                <span aria-hidden="true"></span>
                <span aria-hidden="true"></span>
              </label>
            </div>
              <div className={`navbar-menu ${
                  this.state.showMenu ? "is-active" : ""
                }`}>
                  <Link to="/taylor" onClick={this.pageSwitch} className="navbar-item">
                    Numerical Page
                  </Link>

                <Link to="/products" className="navbar-item">
                  Products
                </Link>
                {this.state.user && this.state.user.accessLevel < 1 && (
                  <Link to="/add-product" className="navbar-item">
                    Add Product
                  </Link>
                )}
                <Link to="/cart" className="navbar-item">
                  Cart
                  <span
                    className="tag is-primary"
                    style={{ marginLeft: "5px" }}
                  >
                    { Object.keys(this.state.cart).length }
                  </span>
                </Link>
                {!this.state.user ? (
                  <Link to="/login" className="navbar-item">
                    Login
                  </Link>
                ) : (
                  <Link to="/" onClick={this.logout} className="navbar-item">
                    Logout
                  </Link>                  
                )}
              </div>
            </nav>
            <Routes>
              <Route exact path="/" element={<ProductList  />} />
              <Route exact path="/login" element={<Login />} />
              <Route exact path="/cart" element={<Shoppingcart/>} />
              <Route exact path="/add-product" element={<AddProduct/>} />
              <Route exact path="/products" element={<ProductList/>} />      
            </Routes>
          </div>
        </Router>
      </Context.Provider>
    );
    else
    return <Numerical page={this.pageSwitch} />    
  }
}
