import { evaluate } from "mathjs";
const FalsePosition = () => {
    return <>
     <div className="hero is-primary">
        <div className="hero-body container">
          <h4 className="title">False Position Method</h4>
        </div>
      </div>
      <br />
      <div className="container">
        <div className="column columns is-multiline">
          <label>Function:<input className="input"
            type="text" id="fx"
            placeholder="f(x)"></input></label>
            
            <label>Lower Value:<input className="input"
            type="text" id="xl"
            placeholder="xL"></input></label>
          
            <label>Upper Value:<input className="input"
            type="text" id="xu"
            placeholder="xU"></input></label>

            <label>Error Es:<input className="input"
            type="text" id="es"
            placeholder="1%"></input></label>

            <label>True Value:<input className="input"
            type="text" id="tv"
            placeholder="T.V (optional)"></input></label>
          
        </div>
       
        <button className="button is-primary" onClick={falsePosition}>Calculate</button>
      </div>
     <br />
     <p id="display"></p>
    </>
}
export default FalsePosition;

function falsePosition(){
  let fx = document.getElementById('fx').value;
  let xl = Number(document.getElementById('xl').value);
  let xu = Number(document.getElementById('xu').value);
  let es = Number(document.getElementById('es').value);
  let tv = Number(document.getElementById('tv').value);
  let xr,ea;
  let exp = fx.replaceAll('ln','log');
  let fxl = evaluate(exp,{x:xl});
  let fxu = evaluate(exp,{x:xu});
  let xr1  = xu - ((fxu * (xl - xu)) / (fxl - fxu))
  let et = 'N/A'
  let fxr = evaluate(exp,{x:xr1});
  let i =1;
  let sign;
  if(fxl * fxr > 0){
    sign = '+';
  }
  else{
    sign = '-'
  }
  if(tv!=0)
  et = Math.abs((tv - xr1)/tv * 100)
  document.getElementById('display').innerHTML='Iteration = '+ i+ '&emsp;&emsp;Sign = '+sign+'&emsp;&emsp;xl = '+xl.toFixed(4)+'&emsp;&emsp;xu = '+xu.toFixed(4)+ '&emsp;&emsp;xr = '+
  xr1.toFixed(4) + '&emsp;&emsp;Ea = N/A' + '&emsp;&emsp;Et = ' + et.toFixed(4);
  if(sign==='+')
  xl = xr1;
  else
  xu = xr1; 
  do{
fxl= evaluate(exp,{x:xl});
fxu = evaluate(exp,{x:xu});
xr  = xu - ((fxu * (xl - xu)) / (fxl - fxu))
fxr = evaluate(exp,{x:xr});
if(fxl * fxr > 0){
  sign = '+';
}
else{
  sign = '-'
}
i++;
ea= Math.abs((xr - xr1) / xr * 100);
xr1=xr;
if(tv!=0)
  et = Math.abs((tv - xr)/tv * 100)
document.getElementById('display').innerHTML+='<br>Iteration = '+i+'&emsp;&emsp;Sign = '+sign+'&emsp;&emsp;xl = '+xl.toFixed(4)+'&emsp;&emsp;xu = '+xu.toFixed(4) +
 '&emsp;&emsp;xr = '+xr.toFixed(4) +'&emsp;&emsp;Ea = '+ea.toFixed(4)+ '&emsp;&emsp;Et = '+et.toFixed(4);
if(sign==='+')
  xl = xr;
  else
  xu = xr; 
  }while(ea>=es)  
}