import { evaluate, to } from "mathjs";
import { Component, createElement } from "react";
class LuDecomp extends Component {
  constructor (props){
    super(props)
    this.state = {
      matb : '',
    }
  }
  handleChange = e => {
    this.setState({[e.target.id]: e.target.value})
    }

  decomp = () => {
  let table = document.getElementById('matrix');
  let matrixA =[]

    for (let i = 0; i< table.rows.length;i++){
     matrixA.push([])
     for(let j = 0; j < table.rows.length;j++){
        matrixA[i].push(0)
     }
      }      
    for(let i = 0; i < table.rows.length;i++){
      for(let j = 0; j < table.rows[0].cells.length; j++){
      if(table.rows[i].cells[j].firstChild.value!=='')
        matrixA[i][j] = Number(table.rows[i].cells[j].firstChild.value);
     }
    }  
    
    let [upper,factors] = toUpperTriangular(matrixA)
    let lower = lowerTriangle(factors)
    let b = this.state.matb.split(',').map(b => Number(b))
    let y=[]
    let y1
    for(let i = 0 ; i < b.length; i++ ){
      y1 = b[i]
      for(let j = 0; j<= i; j++){
        if(i!==j){
          y1 = y1 - lower[i][j] * y[j]
        }
      }
      y1 = y1/lower [i][i]
      y.push(y1) 
    } 
   let x = []
   let x1
    for(let i = y.length-1 ; i >= 0; i-- ){
    x1 = y[i]
    let k = 0
    for(let j = y.length-1; j >= i; j-- ){
      if(i!==j){
        x1 = x1 - upper[i][j] * x[k]
        k++
      }
    }
    x1 = x1/upper [i][i]
    x.push(x1)  // warning [x3,x2,x1] in reverse order 
  } 
x.reverse();
document.getElementById('display').innerHTML='Y = ' +y+ '&emsp;&emsp;X = '+x+'&emsp;&emsp;Upper = '+ [...upper] +'&emsp;&emsp;Lower = '+ [...lower];
  /* document.getElementById('display').innerHTML='Y' + '&emsp;&emsp;X'+'&emsp;&emsp;&emsp;&emsp;Upper'+'&emsp;&emsp;&emsp;&emsp;Lower'; 
  for (let i = 0; i<y.length;i++){
    document.getElementById('display').innerHTML+='<br>' + y[i] + '&emsp;&emsp;'+x[i];
  for(let j = 0;j<y.length;j++){
  document.getElementById('display').innerHTML+='&emsp;&emsp;'+upper[i][j]+'&emsp;&emsp;&emsp;&emsp;'+ lower[i][j]; */
//}
//}

  
  }
  render(){
    return <>
    <div className="hero is-primary">
        <div className="hero-body container">
          <h4 className="title">L U Decomposition Method</h4>
        </div>
      </div>
      <br />
      <div className="container">
        <div className="column columns is-multiline">
          
          <label>Matrix b:<input className="input"
            type="text" id='matb'
            placeholder="as 3,4,5" onChange={this.handleChange}></input>
            </label>

          <table style={{'marginLeft':'50px'}} id="matrix">
            <tbody>
              <tr>
              <td><input type="text" /> </td>
              <td><input type="text" /></td>
              <td><input type="text" /></td>
            </tr>
            <tr>
              <td><input type="text" /></td>
              <td><input type="text" /></td>
              <td><input type="text" /></td>
            </tr>
            <tr>
              <td><input type="text" /></td>
              <td><input type="text" /></td>
              <td><input type="text" /></td>
            </tr>
            </tbody>
          </table>
        </div>
        <button className="button is-primary" onClick={this.decomp}>Calculate</button>
        <button style={{'marginLeft':'250px'}} className="button is-secondary" onClick={addRow}>Add Cell</button>
        <button style={{'marginLeft':'50px'}} className="button is-secondary " onClick={deleteRow}>Delete Cell</button>
      </div>
      <br/>
      <p id="display"></p>
    </>
  }
}
function addRow(){
  let table =document.getElementById('matrix')
  let temp = table.rows[0].cells[0].innerHTML;
  let row = table.rows;
  for(let i = 0 ; i < row.length ;i++){
    row[i].insertCell().innerHTML = temp;
  }
   let newRow = table.insertRow(table.rows.length)
  for(let i = 0 ; i < table.rows[0].cells.length ;i++){
    newRow.insertCell().innerHTML = temp;
  }
}
function deleteRow(){
  let table = document.getElementById('matrix');
	let rowCount = table.rows.length;
	if(rowCount > '1'){
		table.deleteRow(rowCount-1);
    rowCount--;
    for(let i = 0 ; i<rowCount; i++){
      table.rows[i].deleteCell(-1);
	}
}
	else{
		alert('There should be atleast one row');
	}
}
function toUpperTriangular(mat) {
  let factors = []
  for (let i = 0; i < mat.length; i++) {
    factors.push(mat[i][i])
    if(mat[i][i]!==0)
    mat[i] = mat[i].map(num => num/mat[i][i])
      for (let j = i + 1; j < mat.length; j++) {
        if(mat[i][i]==0)
        return [mat,factors];
          const factor = mat[j][i] / mat[i][i];
          factors.push(factor)
          for (let k = i; k < mat.length; k++) {
              mat[j][k] -= factor * mat[i][k];
          }
      }
  }
  return [mat,factors];
}
function lowerTriangle(factors) {
  let table = document.getElementById('matrix');
  let lower = []
  let k =0
  for (let i = 0; i < table.rows.length; i++){
    lower.push([])
    for(let j = 0 ; j < table.rows.length; j++)
    lower[i].push(0)
  }
  for (let j = 0 ; j < table.rows.length; j++) {
    for (let i = j; i < table.rows.length; i++) {
      lower[i][j] = factors[k]
      k++
    }
  }
  return lower;
}

export default LuDecomp;