import { Component } from "react";
import { evaluate,derivative } from "mathjs";

class Taylor extends Component {
  constructor (props){
    super(props);
    this.state = {
      func: "",
      order: 0,
      x0: 0,
      x : 0,
      tv: 0      
    }
  }
  handleChange = e => this.setState({[e.target.id]: e.target.value})
taylor = () => {
let fx = this.state.func;
let order = Number(this.state.order);
let x0= Number(this.state.x0);
let x = Number(this.state.x);
let tv = Number(this.state.tv);
let derv = [];
let i = 1,j=0;
let et = 'N/A';
fx = fx.replaceAll('ln','log')
let fa = evaluate(fx,{x:x0})
let dx = fx
if(tv!=0)
  et = Math.abs((tv - fa)/tv * 100)
document.getElementById('display').innerHTML='Iteration = '+i+'&emsp;&emsp;Approximate = '+ fa.toFixed(4) + '&emsp;&emsp;Et = '+et;
do{
  dx = derivative(dx,'x').toString();
  derv.push(dx)
  fa = fa + (evaluate(derv[j],{x:x0})) / this.factor(i)* Math.pow((x-x0),i);
  i++;
  j++;
  if(tv!=0)
  et = Math.abs((tv - fa)/tv * 100)
  document.getElementById('display').innerHTML+='<br>Iteration = '+i+'&emsp;&emsp;Approximate = '+ fa.toFixed(4) + '&emsp;&emsp;Et = '+et;
}while(j<order)

let prime = "'"
for(let i =0 ; i < order;i++){
  document.getElementById('display').innerHTML+="<br>f"+prime+"x = "+derv[i];
  prime += "'"
}


//+ '&emsp;&emsp;derivatives = '+derv
// make true error es not needed or ea
//example = f(x) = 4.3e^(-3.1x) + 6.7cos(2x) +7.7x
// x=4.8 x0 = 9.1  answer = 239.726 3rd order 
//let answer = fa + f1a * (x-x0) + (f2x * ((x-x0)*(x-x0)))/2 + (f3x * ((x-x0)*(x-x0)*(x-x0)))/6;
}

factor = n => n ?  (n * this.factor(n-1)) : 1;
  render(){return (
    <>
    <div className="hero is-primary">
        <div className="hero-body container">
          <h4 className="title">Taylor Polynomial</h4>
        </div>
      </div>
      <br />
      <div className="container">
        <div className="column columns is-multiline">
          <label>Function:<input className="input"
            type="text" id='func'
            onChange={this.handleChange}
            placeholder="f(x)"></input></label>
            
            <label>Order:<input className="input"
            type="number" id='order' onChange={this.handleChange}
            placeholder="2nd order"></input></label>
          
            <label>Initial Point:<input className="input"
            type="text" id="x0" onChange={this.handleChange}
            placeholder="x0"></input></label>

            <label>value of x:<input className="input"
            type="text" id="x" onChange={this.handleChange}
            placeholder="x"></input></label>

            <label>True Value:<input className="input"
            type="text" id="tv" onChange={this.handleChange}
            placeholder="T.V(optional)"></input></label>

        </div>
      
        <button className="button is-primary" onClick={this.taylor}>Calculate</button>
      </div>
      <br/>
      <p id="display"></p>
    </>
);}
}
export default Taylor;

