
import { evaluate } from "mathjs";
const Bisection = () => {
    return <>
     <div className="hero is-primary">
        <div className="hero-body container">
          <h4 className="title">Bisection Method</h4>
        </div>
      </div>
      <br />
      <div className="container">
        <div className="column columns is-multiline">
          <label>Function:<input className="input"
            type="text" id="fx"
            placeholder="f(x)"></input></label>
            
            <label>Lower Value:<input className="input"
            type="text" id="xl"
            placeholder="xL"></input></label>
          
            <label>Upper Value:<input className="input"
            type="text" id="xu"
            placeholder="xU"></input></label>

            <label>Error Es:<input className="input"
            type="text" id="es"
            placeholder="1%"></input></label>

            <label>True Value:<input className="input"
            type="text" id="tv"
            placeholder="T.V (optional)"></input></label>
          
        </div>
        <button className="button is-primary" onClick={bisect}>Calculate</button>
       
      </div>
      <br />
      <p id="display"></p>
     
    </>
}
export default Bisection;

function bisect(){
  let fx = document.getElementById('fx').value;
  let xl = Number(document.getElementById('xl').value);
  let xu = Number(document.getElementById('xu').value);
  let es = Number(document.getElementById('es').value);
  let tv = Number(document.getElementById('tv').value);
  let xr,ea;
  let exp = fx.replaceAll('ln','log');
  let xr1  = (xl + xu)/2;
  let fxl = evaluate(exp,{x:xl});
  let fxr = evaluate(exp,{x:xr1});
  let i =1;
  let sign;
  let et = 'N/A'
  if(fxl * fxr > 0){
    sign = '+';
  }
  else{
    sign = '-'
  }
  if(tv!=0)
  et = Math.abs((tv - xr1)/tv * 100)
  document.getElementById('display').innerHTML='Iteration = '+i+'&emsp;&emsp;Sign = '+sign+'&emsp;&emsp;xl = '+xl+'&emsp;&emsp;xu = '+xu+ '&emsp;&emsp;xr = '+xr1+ '&emsp;&emsp;Ea = N/A'+ '&emsp;&emsp;Et = '+et;
  if(sign==='+')
  xl = xr1;
  else
  xu = xr1;
  do{
xr  = (xl + xu)/2;
fxl= evaluate(exp,{x:xl});
fxr = evaluate(exp,{x:xr});
if(fxl * fxr > 0){
  sign = '+';
}
else{
  sign = '-'
}
i++;
ea= Math.abs((xr - xr1) / xr * 100);
xr1=xr;
if(tv!=0)
  et = Math.abs((tv - xr)/tv * 100)
document.getElementById('display').innerHTML+='<br>Iteration = '+i+'&emsp;&emsp;Sign = '+sign+'&emsp;&emsp;xl = '+xl+'&emsp;&emsp;xu = '+xu+ '&emsp;&emsp;xr = '+xr+ '&emsp;&emsp;Ea = '+ea+ '&emsp;&emsp;Et = '+et;
if(sign==='+')
  xl = xr;
  else
  xu = xr;
  }while(ea>=es)  
  //console.log(derivative(fx,'x').toString())
 //let [cos,sin,e,ln] =filtering(exp)
  /* let calc = []
  calc.push()
  console.log(cos,sin,e,ln)
  console.log(exp) */
  //console.log(eval(exp.replaceAll('cos',Math.cos())))
 // let [cos, sin, e,ln] = filtering()
//cos.replaceAll('x',)
 
  //document.getElementById('display').innerHTML+='<br>f('+xr+') = '+exp+'='+eval(exp);
  
}
