  let reg = [/[0-9\*x]*cos\([0-9\*x\-\+\/ ]+\)/g, /[0-9\*x]*sin\([0-9\*x\-\+\/ ]+\)/g, /[0-9\*x]*e\([0-9\*x\-\+\/ ]+\)/g, /[0-9\*x]*ln\([0-9\*x\-\+\/ ]+\)/g];
