import { evaluate } from "mathjs";
const Simple = () => {
    return <>
    <div className="hero is-primary">
        <div className="hero-body container">
          <h4 className="title">Simple Fixed Point Method</h4>
        </div>
      </div>
      <br />
      <div className="container">
        <div className="column columns is-multiline">
          <label>Function:<input className="input"
            type="text" id="fx"
            placeholder="g(x) (not f(x))"></input></label>

            <label>Initial Value:<input className="input"
            type="text" id="x0"
            placeholder="x0"></input></label>
            
            <label>Error Es:<input className="input"
            type="text" id="es"
            placeholder="1%"></input></label>

            <label>True Value:<input className="input"
            type="text" id="tv"
            placeholder="T.V (optional)"></input></label>
          
        </div>
        
        <button className="button is-primary" onClick={simplePoint} >Calculate</button>
      </div>
      <br />
      <p id="display"></p>
    </>
}
export default Simple;


function simplePoint(){
  let fx = document.getElementById('fx').value;
  let es = Number(document.getElementById('es').value);
  let tv = Number(document.getElementById('tv').value);
  let x0 = Number(document.getElementById('x0').value);
  let exp = fx.replaceAll('ln','log');

  let i =1, ea, et='N/A';
  let xi1 = evaluate(exp,{x:x0});
  ea = Math.abs((xi1 - x0) / xi1 * 100)

  if(tv!==0)
  et = Math.abs((tv - xi1)/tv * 100).toFixed(4)

  document.getElementById('display').innerHTML='Iteration = '+i+'&emsp;&emsp;xi+1 = '+ xi1.toFixed(4) + '&emsp;&emsp;Ea = '+ea.toFixed(4) +
   '&emsp;&emsp;Et = '+et;

  do{
    x0=xi1
    xi1 = evaluate(exp,{x:x0});
    ea = Math.abs((xi1 - x0) / xi1 * 100)
    i++;
    if(tv!==0)
    et = Math.abs((tv - xi1)/tv * 100).toFixed(4)
    document.getElementById('display').innerHTML+='<br>Iteration = '+i+'&emsp;&emsp;xi+1 = '+ xi1.toFixed(4) + '&emsp;&emsp;Ea = '+ea.toFixed(4) +
    '&emsp;&emsp;Et = '+et;
  }while(ea>=es)


}