import { evaluate } from "mathjs";

const Muller = () => {
    return <>
    <div className="hero is-primary">
        <div className="hero-body container">
          <h4 className="title">Muller Method</h4>
        </div>
      </div>
      <br />
      <div className="container">
        <div className="column columns is-multiline">
          <label>Function:<input className="input"
            type="text" id="fx"
            placeholder="f(x)"></input></label>
            
            <label>First Point:<input className="input"
            type="text" id="x0"
            placeholder="x0"></input></label>
          
            <label>Second Point:<input className="input"
            type="text" id="x1"
            placeholder="x1"></input></label>

            <label>Third Point:<input className="input"
            type="text" id="x2"
            placeholder="x2"></input></label>

            <label>Error Es:<input className="input"
            type="text" id="es"
            placeholder="1% or iterations(start from 0)"></input></label>

            <label>True Value:<input className="input"
            type="text" id="tv"
            placeholder="T.V (optional)"></input></label>
          
        </div>
      
        <button className="button is-primary" onClick={mule}>Calculate</button>
      </div>
      <br />
      <p id="display"></p>
    </>
}
export default Muller;

function mule(){
  let fx = document.getElementById('fx').value;
  let es = Number(document.getElementById('es').value);
  let tv = Number(document.getElementById('tv').value);
  let x0 = Number(document.getElementById('x0').value);
  let x1 = Number(document.getElementById('x1').value);
  let x2 = Number(document.getElementById('x2').value);
  let exp = fx.replaceAll('ln','log');
  let i=1,et='N/A';
  let a,b,c,xi1;
  let h0,h1,d0,d1;

  h0 = x1-x0;
  h1=x2-x1;
  d0 = (evaluate(exp,{x:x1}) - evaluate(exp,{x:x0})) / h0;
  d1 = (evaluate(exp,{x:x2}) - evaluate(exp,{x:x1})) / h1;
  a= (d1 - d0) / (h1+h0);
  b = a * h1 + d1;
  c = evaluate(exp,{x:x2});
  let neg = b - Math.sqrt(b*b - 4 * a * c);
  let pos = b + Math.sqrt(b*b - 4 * a * c);
  if(Math.abs(neg) > Math.abs(pos)){
     xi1 = x2 - (2*c / neg);
  }else{
     xi1 = x2 - (2*c / pos);
  }
  let ea = Math.abs((xi1 - x2) / xi1 * 100);
  if(tv!==0)
  et = Math.abs((tv - xi1)/tv * 100).toFixed(4);
  document.getElementById('display').innerHTML='Iteration = '+i+'&emsp;&emsp;x0 = '+ x0.toFixed(4) + '&emsp;&emsp;x1 = '+x1.toFixed(4) +
   '&emsp;&emsp;x2 = '+x2.toFixed(4) +'&emsp;&emsp;x3 = '+xi1.toFixed(4) +'&emsp;&emsp;a = '+a.toFixed(4)+'&emsp;&emsp;b = '+b.toFixed(4)+
   '&emsp;&emsp;c = '+c.toFixed(4)+'&emsp;&emsp;h0 = '+h0.toFixed(4)+
   '&emsp;&emsp;h1 = '+h1.toFixed(4)+'&emsp;&emsp;d0 = '+d0.toFixed(4)+'&emsp;&emsp;d1 = '+d1.toFixed(4) + '&emsp;&emsp;Ea = '+ea.toFixed(4) +
   '&emsp;&emsp;Et = '+et;

   while(i<=es){
    x0 = x1
    x1 = x2
    x2 = xi1
    h0 = x1-x0;
    h1=x2-x1;
    d0 = (evaluate(exp,{x:x1}) - evaluate(exp,{x:x0})) / h0;
    d1 = (evaluate(exp,{x:x2}) - evaluate(exp,{x:x1})) / h1;
    a= (d1 - d0) / (h1+h0);
    b = a * h1 + d1;
    c = evaluate(exp,{x:x2});
     neg = b - Math.sqrt(b*b - 4 * a * c);
     pos = b + Math.sqrt(b*b - 4 * a * c);
     if(Math.abs(neg) > Math.abs(pos)){
      xi1 = x2 - 2*c / neg;
      }else{
      xi1 = x2 - 2*c / pos;
      }
      ea = Math.abs((xi1 - x2) / xi1 * 100);
      if(tv!==0)
      et = Math.abs((tv - xi1)/tv * 100).toFixed(4);
    i++;
    document.getElementById('display').innerHTML+='<br>Iteration = '+i+'&emsp;&emsp;x0 = '+ x0.toFixed(4) + '&emsp;&emsp;x1 = '+x1.toFixed(4) +
   '&emsp;&emsp;x2 = '+x2.toFixed(4) +'&emsp;&emsp;x3 = '+xi1.toFixed(4) +'&emsp;&emsp;a = '+a.toFixed(4)+'&emsp;&emsp;b = '+b.toFixed(4)+
   '&emsp;&emsp;c = '+c.toFixed(4)+'&emsp;&emsp;h0 = '+h0.toFixed(4)+
   '&emsp;&emsp;h1 = '+h1.toFixed(4)+'&emsp;&emsp;d0 = '+d0.toFixed(4)+'&emsp;&emsp;d1 = '+d1.toFixed(4) + '&emsp;&emsp;Ea = '+ea.toFixed(4) +
   '&emsp;&emsp;Et = '+et;
   }

}